import React, { Component } from 'react';

import Emoji from './Emoji';

import './TileToggle.css';

class TileToggle extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);

    this.state = {
      isNightMode: false,
    };
  }

  toggle() {
    this.props.handleToggle && this.props.handleToggle();
    this.setState({
      isNightMode: !this.state.isNightMode,
    });
  }

  render() {
    const { isAnimating } = this.props;
    const toggle = !this.state.isNightMode ? 'toggle-left' : 'toggle-right';
    return (
      <div
        className={`tile-toggle ${isAnimating ? 'toggle-disable' : ''}`}
        onClick={this.toggle}
      >
        <div
          className={`current-toggle ${toggle}`}
        />
        <Emoji className='moon' symbol='🌚' label='sun' /> 
        <Emoji className='sun' symbol='☀️' label='sun' /> 
      </div>
    );
  }
}

TileToggle.defaultProps = {};

export default TileToggle;
