import React, { Component } from 'react';
import anime from 'animejs';

import Home from './Home';
import TileToggle from './TileToggle';

import './App.css';

const TILE_DIMENSION  = 50; // change for performance

class App extends Component {
  constructor(props) {
    super(props);
    this.handleToggle = this.handleToggle.bind(this);

    this.state = {
      isAnimating: false,
      isNight: false,
      width: 0,
      height: 0,
      gridWidth: 0,
      gridHeight: 0,
    };
  }

  componentDidMount() {
    if (typeof window !== undefined) {
      const height = window.innerHeight;
      const width = window.innerWidth;
      const gridWidth = Math.floor(width / TILE_DIMENSION) + 1;
      const gridHeight = Math.floor(height / TILE_DIMENSION) + 1;

      this.setState({
        width,
        height,
        gridWidth,
        gridHeight,
      });
    }
  }

  cascade() {
    const { gridWidth, gridHeight, isNight } = this.state;

    this.setState({
      isAnimating: true,
    });

    let timeline = anime.timeline()
      .add({
        targets: '.tile',
        background: isNight ? '#fdf6e3' : '#073642',
        scale: [
          {value: .1, easing: 'easeOutSine', duration: 500},
          {value: 1, easing: 'easeInOutQuad', duration: 1200}
        ],
        duration: 3000,
        delay: anime.stagger(200, {grid: [gridWidth, gridHeight]}),
      });

    timeline.finished.then(() => {
      this.setState({
        isAnimating: false,
      });
    });
  }

  handleToggle() {
    this.cascade();
    this.setState({
      isNight: !this.state.isNight,
    });
  }

  render() {
    const { isAnimating, gridWidth, gridHeight } = this.state;
    const numTiles = gridWidth * gridHeight;

    const grid = [...Array(numTiles).keys()].map(i => {
        return (<div
          className='tile-scene'
          style={{height: TILE_DIMENSION, width: TILE_DIMENSION}}
          key={i}
        >
          <div className='tile' />
        </div>)
    });

    return (
      <div className='app'>
        <div
          className='tile-background'
          style={{
            width: gridWidth * TILE_DIMENSION,
            height: gridHeight * TILE_DIMENSION,
          }}
        >
          {grid}
        </div>
        <TileToggle handleToggle={this.handleToggle} isAnimating={isAnimating} />
        <Home />
      </div>
    );
  }
}

export default App;
